
    import Magnify from 'vue-material-design-icons/Magnify.vue'
    
    export default {
        name:"SearchInput",
        components:{
            Magnify
        },
        props:{
            modelValue: {
                type: String,
                default: "",
            },
            placeholder:{
                type: String,
                default:"Rechercher..."
            }
        },
        emits: ['setNewSearchValue']
    }
